import React from 'react'
import {Row, Col, Typography} from 'antd'
import Countdown, {zeroPad} from 'react-countdown';
const { Title } = Typography;
const renderer = ({ hours, minutes, seconds, completed }) => {
    if (completed) {
      return (<Title level={2} style={{color:'#b8b8b8'}}>Vote started</Title>);
    } else {
      return <Title level={2} style={{color:'#b8b8b8'}}>{zeroPad(hours)}:{zeroPad(minutes)}:{zeroPad(seconds)}</Title>;
    }
  };
class Info extends React.Component {
    date(){
      var today = new Date();
      let time = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate() + ' 9:55';
      return time
    }
    render(){
        return(
       <>     <Row justify="center">  <Countdown
       date={  this.date()}
       intervalDelay={0}
       renderer={renderer}
     /></Row>
     <br></br>
     <br></br>
     <br></br>
            <Row justify="center"> <Title level={2}>Regulamin</Title></Row>
            <Row justify="center"><Col span={7}></Col> <Col span={10}>
            <Title level={5}>   1. Gosowanie rozpoczyna si kadego dnia o godzinie 9:30 i koczy o 10:30. </Title>
                </Col><Col span={7}></Col></Row>
            <Row justify="center"><Col span={7}></Col> <Col span={10}>
            <Title level={5}>    2. Aby uczestniczy w gosowaniu naley zalogowa si uprzednio otrzymanym hasem. </Title>
                </Col> <Col span={7}></Col></Row>
                
                <Row justify="center"><Col span={7}></Col> <Col span={10}>
            <Title level={5}>   3. Aby odda gos naley zaznaczy wybran restauracje z listy.</Title> 
                </Col> <Col span={7}></Col></Row>
            <Row justify="center"><Col span={7}></Col> <Col span={10}>
            <Title level={5}>   4. Osoba, ktra oddaa/zmienia gos jako ostatnia jest zobowizana zamawia jedzenie.</Title> 
                </Col> <Col span={7}></Col></Row>
                <Row justify="center"><Col span={7}></Col> <Col span={10}>
            <Title level={5}>   5. W przypadku remisu, restauracja jest wybierana losowo z restauracji o najwikszej liczbie gosw.</Title> 
                </Col> <Col span={7}></Col></Row>
                <Row justify="center"><Col span={7}></Col> <Col span={10}>
            <Title level={5}>   6. Aby uatwi zwrot pienidzy, mona doda w profilu numer konta bankowego/ numer telefonu na ktry pienidze maj by zwrcone.</Title> 
                </Col> <Col span={7}></Col></Row>
                <Row justify="center"><Col span={7}></Col> <Col span={10}>
            <Title level={5}>   7. Po gosowaniu wywietla si informacja o wybranej restauracji wraz z danymi osoby zamawiajcej.</Title> 
                </Col> <Col span={7}></Col></Row>
                <Row justify="center"><Col span={7}></Col> <Col span={10}>
            <Title level={5}>   8. Wszystkie pytania kierowa do Piotra Radlaka.</Title> 
                </Col> <Col span={7}></Col></Row>
        </>
        )
    }
}
export default Info